.modal.mod-browser-packs {
  width: 80vw;
  max-width: 80vw;
}
.modal.mod-browser-packs .modal-content {
  justify-content: center;
}
.modal.mod-browser-packs .modal-content .icon-text {
  margin-left: 4px;
  position: relative;
  top: -1px;
}
.modal.mod-browser-packs .modal-content > .loading {
  display: flex;
  flex: auto;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.modal.mod-browser-packs .modal-content > .loading > .loading-indicator + div {
  margin: 0 20px;
  font-size: medium;
}
.modal.mod-browser-packs .modal-content > .icon-pack-list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}
.modal.mod-browser-packs .modal-content > .icon-pack-list .pack-manifest {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 260px;
  padding: 15px 10px;
  background: var(--background-secondary-alt);
  margin: 15px 10px;
  border-radius: 10px;
}
.modal.mod-browser-packs .modal-content > .icon-pack-list .pack-manifest .pack-manifest-title {
  text-align: center;
  font-weight: bold;
}
.modal.mod-browser-packs .modal-content > .icon-pack-list .pack-manifest .pack-manifest-details {
  font-size: 0.9em;
  color: var(--text-muted);
  display: flex;
  text-align: center;
}
.modal.mod-browser-packs .modal-content > .icon-pack-list .pack-manifest .pack-manifest-details > :not(:last-child) {
  margin-right: 5px;
}
.modal.mod-browser-packs .modal-content > .icon-pack-list .pack-manifest .pack-manifest-desc {
  max-width: 200px;
  height: auto;
}
.modal.mod-browser-packs .modal-content > .icon-pack-list .styles-list {
  border-radius: 10px;
  padding: 5px;
  background-color: var(--background-primary);
  display: flex;
  align-self: stretch;
  flex-direction: column;
  flex-wrap: nowrap;
  margin: 10px 10px 0;
  text-align: center;
}
.modal.mod-browser-packs .modal-content > .icon-pack-list .styles-list > :not(:last-child) {
  margin-bottom: 5px;
}
.modal.mod-browser-packs .modal-content > .icon-pack-list .style-info {
  border-radius: 10px;
  background-color: var(--background-primary-alt);
  font-size: small;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
}
.modal.mod-browser-packs .modal-content > .icon-pack-list .style-info > * {
  display: flex;
  flex-wrap: wrap;
  align-content: center;
  justify-content: space-evenly;
}
.modal.mod-browser-packs .modal-content > .icon-pack-list .style-info .style-info-title {
  flex-grow: 1;
  max-width: 5em;
  line-height: 1.2em;
  margin-left: 10px;
  text-transform: capitalize;
}
.modal.mod-browser-packs .modal-content > .icon-pack-list .style-info .style-info-title .style-info-pack-id {
  text-transform: none;
}
.modal.mod-browser-packs .modal-content > .icon-pack-list .style-info .style-info-title .style-info-pack-id::before {
  content: "(";
}
.modal.mod-browser-packs .modal-content > .icon-pack-list .style-info .style-info-title .style-info-pack-id::after {
  content: ")";
}
.modal.mod-browser-packs .modal-content > .icon-pack-list .style-info .style-info-details {
  flex-grow: 1;
}
.modal.mod-browser-packs .modal-content > .icon-pack-list .style-info .style-info-button-container {
  flex-shrink: 0;
}
.modal.mod-browser-packs .modal-content > .icon-pack-list .style-info .style-info-button-container button {
  padding: 6px;
  margin: 5px 5px 5px 0;
}
