.isc-icon-manager .icons {
  margin-top: 10px;
  display: grid;
  grid-auto-rows: auto;
  grid-auto-columns: -webkit-max-content;
  grid-auto-columns: max-content;
  grid-template-columns: repeat(auto-fill, minmax(90px, 1fr));
  grid-column-gap: 1rem;
  grid-row-gap: 1rem;
  text-align: center;
}
.isc-icon-manager .icons .item {
  outline: none;
}
.isc-icon-manager .icons .item .icon {
  min-height: 64px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: white;
  border-radius: 6px;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  border: 2px solid transparent;
  font-size: 1.6em;
}
.isc-icon-manager .icons .item .icon > img {
  width: 1em;
  height: 1em;
}
.isc-icon-manager .icons .item .name {
  height: 49px;
  display: flex;
  justify-content: center;
  align-content: center;
  flex-direction: column;
}
.isc-icon-manager .icons .item .name span {
  font-size: 0.7em;
  overflow: hidden;
  word-wrap: break-word;
}
.isc-icon-manager .icons .item .name textarea {
  margin-top: 5px;
  width: 100%;
  padding: 0 0.5em;
  font-size: 14px;
}
.isc-icon-manager .icons .item .name textarea:disabled {
  padding: 0;
  font-size: 16px;
  border: hidden;
  text-align: center;
  background: transparent;
}
.isc-icon-manager .icons .item .buttons button {
  padding: 4px 6px;
  margin: 0px 1px;
}
