.mod-settings .isc-add-pack-input {
  margin-right: 5px;
}
.mod-settings .isc-settings-custom-icon .dragover {
  position: relative;
  background-color: var(--shade-10);
  border-radius: 5px;
}
.mod-settings .isc-settings-custom-icon .dragover:before {
  content: "Drop SVG icon(s) here";
  font-size: 16px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: var(--text-normal);
}
.theme-dark .mod-settings .isc-settings-custom-icon .dragover {
  background-color: var(--shade-40);
}
.mod-settings .isc-settings-custom-icon .dragover > * {
  filter: blur(50px);
}
